import React from 'react'
import PropTypes from 'prop-types'
import * as d3 from 'd3'
import cx from 'classnames'
import './style.scss'

export default class RankScroller2 extends React.Component {
  static defaultProps = {
    data: [],
    showCount: 7,
    interval: 3200,
    left: 120,
    bottom: 32
  }
  constructor (props) {
    super(props)
    this.ref = React.createRef()
    this.margin = { top: 0, right: 0, bottom: props.bottom, left: props.left }
  }

  componentDidMount () {
    this.initAxis() // 只做一次初始化

    this.drawAxis()
    this.startAnimateData()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.data !== this.props.data) {
      this.drawAxis()
      this.startAnimateData()
    } else if (prevProps.style !== this.props.style) {
      this.drawAxis()
    }
  }

  initAxis = () => {
    let g = this.svg.append('g').attr('transform', 'translate(' + this.margin.left + ',' + this.margin.top + ')')
    g.append('g').attr('class', 'x-axis')
    g.append('g').attr('class', 'y-axis')
  }

  drawAxis = () => {
    const props = this.props
    let { data, showCount } = props

    let width = this.svg.node().parentNode.offsetWidth - this.margin.left
    let height = this.svg.node().parentNode.offsetHeight - this.margin.bottom

    let max = d3.max(data, v => v.value)
    let xScale = d3.scaleLinear().domain([0, max]).range([0, width])
    let yScale = d3.scaleLinear().domain([0, showCount]).range([height, 0])
    this.percentScale = d3.scaleLinear().domain([0, max]).range([0, 100])

    let xAxis = d3.axisBottom(xScale).ticks(5).tickSizeOuter(0)
    let yAxis = d3.axisLeft(yScale).tickValues(Array.from({length: showCount + 1}).map((v, i) => i)).tickSizeOuter(0)

    this.svg.select('g.x-axis').attr('transform', 'translate(0,' + height + ')').transition().call(xAxis)
    let yAxisElement = this.svg.select('g.y-axis').call(yAxis)

    this.svg.selectAll('g.x-axis g.tick line.grid').remove()

    this.svg.selectAll('g.x-axis g.tick:not(:first-of-type)')
      .append('line')
      .attr('class', 'grid')
      .attr('y2', -height)

    yAxisElement.selectAll('text,line').remove()

    this.svg.selectAll('g.y-axis g.tick:not(:first-of-type):not(:last-of-type)')
      .append('line')
      .attr('class', 'grid')
      .attr('x2', width)
  }

  startAnimateData = () => {
    const props = this.props
    clearInterval(this.timerKey)

    // 重置，清空显示的数据
    this._renderData([])

    const {showCount} = props

    let data = props.data.map((value, innerIndex) => {
      return {...value, innerIndex}
    })

    let showData = data.slice(0, showCount)
    let index = showCount
    this._renderData(showData)

    if (data.length > showCount) {
      this.timerKey = setInterval(() => {
        showData.shift()
        index = index + 1 > data.length - 1 ? 0 : index + 1
        showData.push(data[index])
        this._renderData(showData)
      }, this.props.interval)
    }
  }

  _renderData = (data) => {
    const {showCount, left} = this.props

    let height = this.svg.node().parentNode.offsetHeight - this.margin.bottom

    let divide = height / showCount
    function barTop (i) {
      return divide / 2 + i * divide + 'px'
    }

    const container = d3.select(this.ref.current)

    let selection = container.selectAll('div.item').data(data, v => v.innerIndex)

    if (data.length === 0) {
      selection.exit().remove()
      return
    }

    let enters = selection.enter().append('div').attr('class', 'item')

    const transition = d3
      .transition()
      .duration(500)
      .ease(d3.easeLinear)

    enters.append('label').text(v => v.name).style('width', left + 'px')
    enters.append('div').style('--width', d => `calc(${this.percentScale(d.value) + '%'} - 8px)`).style('margin-left', '8px')

    enters.style('top', (d, i) => {
      return barTop(i + 1)
    }).transition(transition)
      .style('top', function (d, i) {
        return barTop(i)
      })

    selection.transition(transition)
      .style('top', function (d, i) {
        return barTop(i)
      })

    selection.exit()
      .transition(transition)
      .style('top', () => {
        return barTop(-1)
      }).remove()
  }

  componentWillUnmount () {
    clearInterval(this.timerKey)
  }

  render () {
    const props = this.props
    return (
      <div style={props.style} className={cx('dui-rank-scroller2', props.className)}>
        <svg width='100%' height='100%' ref={r => (this.svg = d3.select(r))} />
        <div ref={this.ref} className='item-container' style={{...this.margin, left: 0, right: 0}} />
      </div>
    )
  }
}

RankScroller2.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired
  })).isRequired
}
